import React from "react";
import { graphql } from "gatsby";
import Layout from "../components/layout";

export default (props) => {
    const data = props.data.cvJson;

    return (
        <Layout>
            <header className="py-12 border-gray-400 border-solid border-t-4">
                <div className="max-w-4xl mx-auto">
                    <h2 className="capitalize text-6xl font-bold">{data.title}</h2>
                    {
                        data.description && <h4 className="text-xl">{data.description}</h4>
                    }
                    {
                        data.profile && <div>
                            <h4 className="horizontal-text text-xl">{data.professional_title}</h4>
                            <div className="flex items-center flex-1 p-8">
                                <div className="flex-1">{data.profile}</div>
                            </div>
                            <div className="flex items-center flex-1 p-8">
                                <div className="flex-1">{data.email}</div>
                            </div>
                        </div>
                    }
                </div>
            </header>
            {
                data.items && <ul>
                    {
                        data.items.map((item, index) => (
                            <li className="bg-white shadow mt-4 flex" key={index}>
                                <p className="vertical-text">{data.slug}</p>
                                <div className="flex items-center flex-1 p-8">
                                    <div className="flex-1">{item} {index}</div>
                                </div>
                            </li>
                        ))
                    }
                </ul>
            }
        </Layout>
    )
}

export const query = graphql`
    query($slug: String) {
        cvJson(slug: {eq: $slug}) {
            slug
            title
            description
            items
            name
            professional_title
            email
            profile
        }
    }
`